<template>
  <div >
    <div ref='lis' class="ding">
      <!-- 滚动 -->
      <ul ref='lis' class="uul">
      <li v-for="(item,index) in list" :key="index" :class="{hang:item.isme}"><span>{{item.isme ? ':我':'小智：'}}</span><span>{{item.msg}}</span></li>
    </ul>
    </div>
    <div class="shuru">
      <van-field v-model="sms" center clearable  placeholder="请输入短信验证码">
        <van-button slot="button" size="small" type="primary"  @click="send" >发送</van-button>
      </van-field>
    </div>
  </div>
</template>

<script>
// 聊天持久化保存       自动滚动到最下方
import io from 'socket.io-client'
import { setItem, getItem } from '@/utils/storage'
export default {
  data () {
    return {
      list: getItem('chatList') || [],
      sms: '',
      socket: null
    }
  },
  methods: {
    send () {
      let msg = this.sms
      let data = { msg: msg, timestamp: Date.now() }
      // 向后端发送数据
      this.socket.emit('message', data)
      this.sms = ''
      data.isme = true
      this.list.push(data)
      setItem('chatList', JSON.stringify(this.list))
    }
  },
  watch: {
    list () {
      this.$nextTick(() => {
        console.dir(this.$refs.lis)
        let listContainer = this.$refs.lis
        listContainer.scrollTop = listContainer.scrollHeight
        console.log(this.$refs.lis.scrollTop)
      })
    }
  },
  created () {
    // 和后端服务建立链接
    const socket = io('http://ttapi.research.itcast.cn')
    this.socket = socket

    // 监听后端推送过来的数据(注，init是可以自定义的，只要和后端约定好可行了！！)
    socket.on('message', data => {
      console.log(data) // 这是后端推送过来的数据
      this.list.push(data)
      setItem('chatList', JSON.stringify(this.list))
    })
  },
  mounted () {
    console.log('渲染')
    this.$nextTick(() => {
      let listContainer = this.$refs.lis
      listContainer.scrollTop = listContainer.scrollHeight
    })
  }
}
</script>

<style lang='less' scoped>
.shuru{
    position: fixed;
    bottom: 50px;
    left: 0;
    right: 0;
}
.hang{
    display: flex;
    // justify-content: flex-start;
    flex-direction: row-reverse;
}
.ding{
  height: 100vh;//https://blog.csdn.net/kouryoushine/article/details/99745904
  overflow-y: scroll;
  padding-bottom: 100px;
}
</style>
